<template>
  <div>
    <MdInput :icon="icon" :type="type" :placeholder="placeholder" class="demo">姓名</MdInput>
  </div>
</template>
<script type="text/ecmascript-6" >
  import MdInput from 'components/MDInput'
  export default {
    data() {
      return {
        icon: 'search',
        type: 'text',
        placeholder: '请输入',
      }
    },
    components: {
      MdInput
    }
  }
</script>
<style lang="scss" scoped>
  .demo {
    width: 200px;
  }
</style>


